.hover-image {
  position: relative;
  overflow: hidden;
  margin-bottom: 30px;
}
.hover-image--square {
  width: 100%;
  height: 0;
  padding-bottom: 100%;
}
.hover-image--square .hover-image__image {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-size: cover;
}

.hover-image__text {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: $color-overlay-dark;
  padding: 15px;
  color: white;
  text-align: center;
}
.hover-image__image {
  transition: 0.25s filter;
  -webkit-transition: 0.25s -webkit-filter;
}
.hover-image:hover .hover-image__image {
  filter: blur(2px);
  -webkit-filter: blur(2px);
}
.hover-image__text__title {
  @extend h2;
  font-size: 1.5em;
  color: white;
  display: block;
  margin-bottom: 0;
  .hover-image--large & {
    font-size: 2em;
  }
}
.hover-image__text__blurb {
  padding-top: 15px;
  display: block;
  width: 100% !important; // Override Bootstrap's hide/show
  overflow: hidden;
}
.hover-image__text__blurb p {
  line-height: 1.1;
}
